/**
 * ----------------------------------------------------------------------------------------
 * Header Styles
 * ----------------------------------------------------------------------------------------
 */

header{
  &.main-header{
    position: relative;
    top: 0;
    width: 100%;
    height: 50px;
    z-index: 10;
    line-height: 50px;
    box-shadow: 0 0 2px rgba(0,0,0,.1);
    background: rgba(255, 255, 255, 0.96);
    @include padding(null 20px);
    h1{
      font-size:14px;
      display: inline-block;
      @include user-select(none);
      a{
        font-weight: 600;
        color:$body-link-color;
        &:hover{
          color:$body-link-hover-color;
          @include transition();
        }
      }
    }
    a{
      &.header-search{
        position: relative;
        display: inline-block;
        font-size:13px;
        font-weight: 400;
        cursor: pointer;
        @include margin(null null null 20px);
      }
    }
  }
}

input[name="test"]{
  position: absolute;
  right: -999em;
  width: 0;
  &:checked{
   & + .search-area{
      opacity: 1 !important;
    }
  }
}

.search-area{
  opacity: 0;
}