/**
 * ----------------------------------------------------------------------------------------
 * Home Styles
 * ----------------------------------------------------------------------------------------
 */

main{
  &[role="home"],&[role="article"]{
    header,.content-profile{
      text-align: center;
    }
  }
}

section{
  &.content-profile{
    @include margin(15px null 120px null);
    .profile-wrap{
      display: inline-block;
      position: relative;
      .author-avatar{
        height: 130px;
        width: 130px;
        @include transition();
        .avatar{
          height: 130px;
          width: 130px;
          background: #ffffff;
          background-repeat:no-repeat;
          background-size:cover;
          @include border-left-radius(100%);
          @include border-right-radius(100%);
        }
      }
      .author-url{
        position: absolute;
        right: 0;
        bottom: 0px;
        a{
          display: block;
          width: 38px;
          height: 38px;
          font-size: 20px;
          line-height: 38px;
          color: #fff;
          @include border-left-radius(100%);
          @include border-right-radius(100%);

          &[href*="facebook.com"]{
            background: $facebook-color;
            span{
              &:before{
                content:$cz-facebook-f;
              }
            }
          }
          &[href*="twitter.com"]{
            background: $twitter-color;
            span{
              &:before{
                content:$cz-twitter;
              }
            }
          }
          &[href*="github.com"]{
            background: $github-color;
            span{
              &:before{
                content:$cz-github;
              }
            }
          }
          &[href*="linkedin.com"]{
            background: $linkedin-color;
            span{
              &:before{
                content:$cz-linkedin;
              }
            }
          }
          &[href*="youtube.com"]{
            background: $youtube-color;
            span{
              &:before{
                content:$cz-youtube;
              }
            }
          }
          &[href*="instragram.com"]{
            background: $instagram-color;
            span{
              &:before{
                content:$cz-instagram;
              }
            }
          }
          &[href*="soundcloud.com"]{
            background: $soundcloud-color;
            span{
              &:before{
                content:$cz-soundcloud;
              }
            }
          }
          &[href*="mailto:"]{
            background: $github-color;
            span{
              &:before{
                content:$cz-envelope-o;
              }
            }
          }
        }
      }
    }
    .bio-wrap{
      text-align: center;
      p{
        @include padding(null 20px);
        @include margin(null null 4%);
      }
    }
  }
  &.content-articles{
    h5{
      @include margin(null null 25px null);
    }
    article{
      &.article--list{
        @include margin(null null 13px null);
      }
    }
  }
}

nav{
  @extend .clear;
  &.pagination{
    ul{
      overflow: hidden;
      margin: 0;
      padding: 0;
      li{
        list-style-type: none;
        float: left;
        @include margin(null null null 5px);
        &:first-child{
          margin-left: 0;
        }
        a{
          color:#333;
          font-size:15px;
          &:hover{
            color:$body-link-hover-color;
            @include transition();
          }
        }
      }
    }
  }
}