/**
 * ----------------------------------------------------------------------------------------
 * Main Styles
 * ----------------------------------------------------------------------------------------
 */

.container{
  position: relative;
  overflow: hidden;
  .content{
    margin: 0 auto;
    max-width: $container-max-width;
    width: 100%;
    &[role="main"]{
      @include padding(100px null 20px 20px);
    }
  }
}